import { Component, ViewChild } from '@angular/core';
import { GooglePlus } from '@ionic-native/google-plus/ngx';
import { NavController, PopoverController } from '@ionic/angular';
import { Socket } from 'ng-socket-io';
import { Storage } from '@ionic/storage';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router'
import { PopoverSettingsComponent } from '../popover-settings/popover-settings.component';
import { LoginService } from '../services/login.service';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})

export class Tab1Page {
	images: any;
	user: any;
	myId = null;
	avatar: any;

	constructor(private googlePlus: GooglePlus,
				public navCtrl: NavController,
				private http: HttpClient,
				private socket: Socket,
				private storage: Storage,
				private loginService: LoginService,
				private activatedRoute: ActivatedRoute,
				public popoverController: PopoverController) { }
			
	@ViewChild('myNav') nav: NavController
		
	ionViewDidEnter(){
	console.log('ngoninit');
	//	this.myId = this.activatedRoute.snapshot.paramMap.get('myid');
	
		this.storage.get('google_user').then(data => {
			console.log(data);
			this.avatar = data.avatar;
			this.user = data;

			this.socket.emit('getImgs', this.user);
			this.socket.on('images', res => {
				console.log(res);
				res.forEach(img => {
					console.log(img);
					this.images = img.image;
				});	
			});
		})
			
		

	
		/*this.storage.get('google_user').then((val) => {
			console.log(val);
		  });*/	  
	}

//Getting images from db	
	getData() {
		console.log('getData');
		this.socket.on('user', (data) => {
			this.images = data;
			console.log(this.images);
			console.log('lol');
		});
	}

	//Settings window on profile page
	async presentPopover(ev: any) {
		const popover = await this.popoverController.create({
		  	component: PopoverSettingsComponent,
		  	event: ev,
		 	translucent: true
		});
		return await popover.present();
	}
}